.todo_container {
  width: 50%;
  margin: 0 auto;
}
.todo {
  margin-top: 20px;
  background: white;
  padding: 10px 20px;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 1px 2px 10px #ddd;
}
.todo input {
  cursor: pointer;
  width: 50%;
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
}
.button-complete,
.button-delete,
.button-edit {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  border-radius: 50%;
}
#i {
  font-size: 35px;
}
.button-complete {
  color: #95faa3;
  margin-right: 10px;
}
.button-edit {
  color: #56cfff;
  margin-right: 10px;
}
.button-delete {
  color: rgb(255, 122, 98);
}
@media screen and (max-width: 667px) {
  .task_container {
    width: 90%;
  }
  .task {
    margin-right: 0px;
  }
}
input:focus,
button:focus {
  outline: none;
}
.error{
  font-size: 12px;
  margin-left: 9px;
  color: red;
}
.hide{
  display: none;
}


@media screen and (max-width: 667px) {
 .todo{
   flex-direction: column;
   padding: 0%;
   margin: 2rem 0px;
 }
 .todo input{
   width: 100%;
 }
 .todo_container{
   padding: 0% 2px;
   width: 100%;
 }
 .btn_container{
   display: flex;
   flex-direction: column;
 }
 .button-34{
   width: 100% !important; 
   margin: 4px 0px;
 }
}




